.navbar{
    background-color:aqua;
    height: 90px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: sticky;
    top: 0;
    z-index: 20;

}
.nav-container{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 80px;
    max-width: 1500px;
}

.nav-logo{
    color:brown;
    align-items:center;
    margin-left:20px;
    cursor:pointer;
    text-decoration:none;
    font-size: 2rem;
    flex-grow: 1;
}

.nav menu{
    display:flex;
    list-style: none;
    text-align: center;
    
}